.about {
    text-align: center;
    margin-top: 20px;
    padding: 0 20px;
    width: 100%;
}

.about h1 {
    font-size: 25px;
}
.about h2 {
    margin-top: 20px;
    font-size: 20px;
}

.course-title {
    color: rgb(128, 204, 240);
}
.about ul{
    text-align: left;
    margin-top: 20px;
    margin-left: 50px;
}

.about ul li {
    margin: 10px 0;
    color: rgb(80, 80, 80);
    font-style: italic;
}
   
.about ul li.update {
    color: red;
    font-weight: bold;
}

.contact {
    margin-top: 40px;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    color: #fff;
    font-weight: bold;
    border-radius: 2px;
    background-color: rgb(59, 59, 59);
}
.contact i {
    font-size: 30px;
    margin: 20px;
}

.contact a {
    text-decoration: none;  
    color: #fff;
}


