.navbar {
    background-color: #000;
}

.navbar ul {
    list-style: none;
    display: -ms-flexbox;
    display: flex;
}

.navbar ul li {
    padding: 10px;
}

.link {
    padding: 10px;
    text-decoration: none;
    color: #ededed;
    font-size: 12px;
}

.link:hover {
    background-color: rgb(34, 33, 33);
    font-weight: bold;
    color: #fff;
}


